/* Fondo del modal con opacidad negra */
.formaparte-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 902;
    overflow-y: auto; /* Barra de desplazamiento vertical */
}

/* Estilo del modal */
.formaparte-modal {
    background-color: var(--color-container);
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    position: relative;
    display: flex;
    flex-direction: column;
    max-height: 90vh; /* Limita la altura del modal */
    overflow-y: auto; /* Agrega barra de desplazamiento vertical */
    z-index: 902;
}

/* Botn de cerrar especfico para este modal */
.formaparte-modal .formaparte-close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    background-color: var(--color-button-negation);
    color: white;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.formaparte-modal .formaparte-close-btn:hover {
    background-color: var(--color-button-negation-hover);
}

/* Encabezado del modal */
h2 {
    text-align: center;
    margin-top: 10px;
    color: var(--color-text);
}

/* Estilo de los campos de entrada y labels */
.formaparte-group {
    margin-bottom: 15px;
}

.labelform {
    color: var(--color-text);
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
}

input,
select {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
    background-color: var(--color-container);
    color: var(--color-text);
}

/* Agregar barra de desplazamiento vertical al select */
select {
    overflow-y: auto;
}

/* Estilo de la barra de desplazamiento */
::-webkit-scrollbar {
    width: 12px; /* Ancho de la barra de desplazamiento */
}

::-webkit-scrollbar-track {
    background: var(--color-container-hover); /* Color del fondo de la barra de desplazamiento */
}

::-webkit-scrollbar-thumb {
    background-color: var(--color-buttons); /* Color de la barra de desplazamiento */
    border-radius: 10px; /* Redondear los bordes de la barra de desplazamiento */
    border: 3px solid var(--color-container-hover); /* Espacio alrededor de la barra de desplazamiento */
}

::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-buttons-hover); /* Color de la barra de desplazamiento al pasar el mouse */
}

/* Botn de envo */
button {
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: var(--color-button-confirmation);
    color: white;
    cursor: pointer;
}

button:hover {
    background-color: var(--color-button-confirmation-hover);
}

/* Contenedor principal de la pregunta con radio buttons */
.checkformspoti-group {
    display: flex; /* Usamos flexbox para organizar el contenido */
    flex-direction: column; /* Alinear los elementos verticalmente */
    margin-bottom: 15px; /* Separacin entre grupos */
}

/* Estilo para cada opcin de radio button */
.checkformspoti {
    color: var(--color-text); /* Texto en blanco */
    margin-bottom: 10px; /* Separacin entre opciones */
    display: flex; /* Usamos flexbox para el alineado del radio button y el texto */
    align-items: center; /* Alinear verticalmente */
    cursor: pointer; /* Cursor al pasar sobre las opciones */
}

/* Margen derecho para los radio buttons */
.checkformspoti input {
    margin-right: 30px; /* Espacio entre el radio button y el texto */
}

/* Estilo para el input radio */
.spotify-radio {
    accent-color: var(--color-button-confirmation); /* Cambia el color del radio button a verde */
    width: 20px; /* Tamao personalizado del radio button */
    height: 20px; /* Tamao personalizado del radio button */
    cursor: pointer; /* Cambio de cursor cuando pasas sobre el radio button */
    margin-right: 10px; /* Espacio entre el radio button y el texto */
    transition: all 0.3s ease; /* Animacin suave al interactuar */
}

.spotify-radio:hover {
    transform: scale(1.2); /* Aumenta el tamao del radio button al pasar el mouse */
}

.spotify-radio:checked {
    background-color: var(--color-button-confirmation); /* Fondo verde cuando est seleccionado */
    border-color: var(--color-button-confirmation); /* Borde verde cuando est seleccionado */
}

/* Estilo para el checkbox */
.checkboxinformation {
    margin-right: 10px; /* Espacio entre el checkbox y el texto */
    cursor: pointer; /* Cursor al pasar sobre el checkbox */
    transform: scale(1.1); /* Un pequeo aumento en el tamao del checkbox */
    transition: all 0.3s ease; /* Suavizado de la transicin al interactuar */
}

/* Aumentar el tamao del checkbox al pasar el mouse */
.checkboxinformation:hover {
    transform: scale(1.2); /* Aumenta el tamao al pasar el mouse */
}

/* Estilo para el texto junto al checkbox */
.checkboxinformation + span {
    font-weight: bold; /* Hace el texto en negrita */
    color: var(--color-text); /* Color blanco para el texto */
    cursor: pointer; /* Cambio de cursor al pasar sobre el texto */
    font-size: 16px; /* Tamao de fuente */
}

/* Cambia el color del texto a verde cuando el checkbox est seleccionado */
.checkboxinformation:checked + span {
    color: var(--color-button-confirmation); /* Cambia el color del texto a verde cuando el checkbox est seleccionado */
}

/* Contenedor principal del checkbox */
.checkformcheckbox-group {
    display: flex; /* Usamos flexbox para organizar el contenido */
    flex-direction: column; /* Alinear los elementos verticalmente */
    margin-bottom: 15px; /* Separacin entre grupos */
}

/* Estilo para el label del checkbox */
.checkformcheckbox {
    color: var(--color-text); /* Texto en blanco */
    font-weight: bold; /* Texto en negrita */
    margin-bottom: 10px; /* Separacin entre opciones */
    display: flex; /* Usamos flexbox para el alineado del checkbox y el texto */
    align-items: center; /* Alinear verticalmente */
    cursor: pointer; /* Cursor al pasar sobre las opciones */
}

/* Margen derecho para los checkboxes */
.checkformcheckbox input {
    margin-right: 30px; /* Espacio entre el checkbox y el texto */
}

/* Estilo para el input checkbox */
.checkbox-input {
    appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid var(--color-buttons); /* Use variable for border color */
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px; /* Espacio entre el checkbox y el texto */
    transition: all 0.3s ease; /* Animacin suave al interactuar */
}

.checkbox-input:checked::before {
    content: '';
    color: white;
    font-size: 14px;
    position: absolute;
}

.checkbox-input:checked {
    background-color: var(--color-buttons); /* Use variable for background color */
    border-color: var(--color-buttons); /* Use variable for border color */
}

.checkbox-input:hover {
    transform: scale(1.2); /* Aumenta el tamao al pasar el mouse */
}

.checkbox-input:checked + span {
    color: var(--color-buttons); /* Use variable for text color */
}

.Toastify__toast {
    position: fixed !important;  /* Asegura que el toast est fijo en la pantalla */
    top: 20px;  /* Ajusta la distancia desde la parte superior */
    z-index: 999999;  /* Asegura que est por encima de otros elementos */
    transition: transform 0.3s ease-in-out;  /* Transicin para suavizar el movimiento */
    max-width: 90%;  /* Asegura que el toast se vea bien en pantallas pequeas */
    box-sizing: border-box;  /* Evita que el padding y el borde desajusten el tamao */
}

/* Ajuste especfico para pantallas pequeas */
@media (max-width: 768px) {
    .Toastify__toast {
        top: 10%;  /* Ajusta la distancia para pantallas pequeas */
    }
}

